import React from 'react';
import Bloglists from '../components/Blog/Bloglists'
import BlogBanner from '../components/Banner/BlogBanner';
import "../components/Blog/blog.css"
import Fondo from '../components/Fondo';
import ReactGA from 'react-ga';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';

    // Configuraci√≥n de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/blog");

const Blog = () => {
    return(
        <div className="body_wrapper blog">
            <MetaDecorator 
                meta_title="metaTitle_Blog"
                meta_description="metaDescription_Blog"
                url="about"                />
            <BlogBanner 
                title_banner2 = "title_banner_blog"
                idBanner="bannerBlog"
                classBanner2="banner_blog"
            />
            <Fondo ClassFondoImg2="d-none" ClassFondoImg3="d-none"/>
            <Bloglists
              subTitle_blog = "subTitle_blog"
              descriSubTitle = "descriSubTitle"
            />

        </div>
    )
}
export default Blog;