import './App.css';
import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { TranslatorProvider } from "react-translate";




/* ---- Pages ----*/
import Home from "./Pages/Home";
import About from "./Pages/About"
//import PrivacyPolicy from "./Pages/Privacy"
import Devops from "./Pages/Devops";
import Jobs from "./Pages/Jobs";
import Scrum from "./Pages/Scrum";
import Contact from "./Pages/Contact";
import Blog from "./Pages/Blog"
import Services from "./Pages/Services"
import NotFound from "./Pages/404";

/* ---- Menu y Footer ----*/
import Menu from './components/Header/Menu';
import Footer from "./components/Footer/Footer";
import Post from "./components/Blog/Blogsingle"
import BlogSingle from './Pages/PagePost';


class App extends Component{
 	
    constructor(props) {
        super(props);
        this.state = {
            language: 'en'
        }; 
        this.languageChange = this.languageChange.bind(this);  
    }
      
    languageChange(event) {
        this.setState({language: event.target.value});
    }


   getLayout(lang){

    const translationsLang = require('./assets/translations/'+lang+'.json');
    
	   return  (    
        <TranslatorProvider translations={translationsLang}>	
                <Router> 
                    <Menu 
                        menu_blog= "menu_blog" menu_jobs= "menu_jobs"   
                        value={this.state.value} language={this.state.language}
                        onChangeLanguage={this.languageChange} 
                        />
                    <Switch>                       
                        <Route exact path={"/"} component={() => <Home language={this.state.language} />} />
                        <Route path="/about" component={About} /> 
                        <Route path="/services" component={Services} /> 
                        <Route path="/contact" component={Contact} />    
                        <Route path="/devops" component={Devops} />                        
                        <Route path="/jobs" component={() => <Jobs language={this.state.language} />} /> 
                        <Route path="/scrum" component={Scrum} />
                        {/* <Route exact path="/privacy-policy" component={() => <PrivacyPolicy language={this.state.language} titlePrivacy="titlePrivacy"/>}/> */}
                        <Route path="/blog" component={Blog} /> 
                        <Route path="/post" component={BlogSingle}>
                            <Route exact path="/post/:url" render={props => <Post {...props} />} />         
                            <Route path="/404" component={NotFound} />
                        </Route>
                        <Route path='*' exact={true} component={NotFound} />
                    </Switch>
                    
                    <Footer language={this.state.language} />
                </Router>
        </TranslatorProvider>
       )
   }

   render(){
	   return(
           this.getLayout(this.state.language)
        );
    };    
}; 


export default App;